<template>
	<form class="m-0" @submit.prevent="submitForm">
		<div class="card">
			<div class="card-body">
				<h4 class="card-title">Prototype Form</h4>
				<p class="card-text text-danger mt-3 mb-5"><i class="fas fa-lock mr-2"></i>Required information</p>

				<div class="row my-3">
					<div class="col">
						<InputForm name="title" 
							type="text" 
							:label="{ text: 'Title', iconClass: 'fas fa-file-alt' }" 
							:required="true" 
							placeholder="Prototype title or name" 
							:value="prototype.title"/>
					</div>
				</div>  
				<div class="row my-3">
					<div class="col-6">
						<MultipleSelectForm name="tag"
							:label="{ text: 'Primary Tag', iconClass: 'fa fa-tags' }"
							:options="Object.keys(tags)"
							:required="true"
							:value="prototype.tag"/>
					</div>
					<div class="col-6">
						<SelectForm name="subtag"
							v-if="availableSubtags.length > 0"
							:label="{ text: 'Secondary Tag', iconClass: 'fa fa-tag' }"
							:options="availableSubtags"
							:required="false"
							:value="prototype.subtag"/>
					</div>  
				</div>

				<div class="row my-3">
					<div class="col">
						<TextareaForm name="description"
							:label="{ text: 'Description', iconClass: 'fa fa-align-left' }"
							:required="true"
							placeholder="Short description to introduce prototype applications..."
							:value="prototype.description"/>
					</div>
				</div>

				
				<LinkListInput name="technologies" 
					:label="{ text: 'Technologies', iconClass: 'fas fa-microchip' }" 
					:value="prototype.technologies" 
					:required="true"/>
			</div>

			<div class="card-footer">
				<p class="card-text text-info"><i class="fas fa-lock-open mr-2"></i> Optional information</p>

				<div class="row my-3">
					<div class="col">
						<LinkListInput name="graphics" 
							:label="{ text: 'Graphic material', iconClass: 'far fa-chart-bar'}"
							:value="prototype.graphics"/>
					</div>
				</div>

				<div class="row my-3">
					<div class="col">
						<TextareaForm name="scope"
							:label="{ text: 'Scope', iconClass: 'fas fa-binoculars' }"
							placeholder="The scope that current prototype is thinked for..."
							:value="prototype.scope"/>
					</div>
				</div>

				<div class="row my-3">
					<div class="col">
						<StepsInput name="steps"
							:label="{ text: 'First steps', iconClass:'fa fa-list-ol' }" 
							:value="prototype.steps"/>
					</div>
				</div>
					
				<button type="submit" class="btn btn-pill btn-primary btn-block mt-5 mb-4">
					<i class="fas fa-save mr-2"></i>
					Save
				</button>
			</div>
		</div>
	</form>
</template>

<script>
import InputForm from '@/components/InputForm'
import SelectForm from '@/components/SelectForm'
import MultipleSelectForm from '@/components/MultipleSelectForm'
import TextareaForm from '@/components/TextareaForm'
import LinkListInput from '@/components/LinkListInput'
import StepsInput from '@/components/StepsInput'

export default {
	data(){
		return{
			prototypeKey: "",
			availableSubtags: [],
			prototype: {
				title: "",
				tag: "",
				subtag: "",
				description: "",
				scope: "",
				graphics: [],
				steps: []
			},
			tags: {
				"Frontend": [], 
				"Backend": [], 
				"Mobile": [], 
				"Big Data": [],
				"IoT": [],
				"Cloud": [], 
				"DevOps": [], 
				"Security": [], 
				"Blockchain": [], 
				"DevOps": [], 
				"Human Computer Interfaces": [
					"VR", 
					"AR", 
					"Chatbots", 
					"Voice conversationals"
				], 
				"UX": [], 
				"UI": [],
				"Machine Intelligence": [
					"Reinforcement learning", 
					"Natural Lenguaje Proccesing", 
					"Recomender System", 
				  	"Artificial Vision", 
					"Cloud Machine Learning",
					"Data Augmented",
				  	"Generative Models",
					"Deep Learning"
				] 
			},
		}
	},
	mounted() {
		if (this.$route.params.id && this.$route.params.id !== "new") this.loadPrototype(this.$route.params.id);

		this.$on("change", data => {
			if (data.name === "tag") this.loadSubtags(data.value);
		});
	},
	beforeRouteLeave(to, from, next) {
		if (this.$el.classList.contains("submited")) next();
		else if (confirm("Changes will be discared... Do you want to continue?")) next();
	},
	methods: {
		loadPrototype(id) {
			this.$database.get(`prototypes/${ id }`)
				.then(prototype => {
					prototype.id = id;
					this.prototype = prototype;

					if (prototype.tag) this.loadSubtags(prototype.tag);
				})
				.catch(err => {
					console.error(err);
					this.$eventbus.$emit("alert", { type: "danger", message: "Error getting prototype info. Check console for more details" })
				});
		},
		loadSubtags(tag) {
			if (!Array.isArray(tag)) tag = [ tag ];

			this.prototype.tag = tag;
			this.availableSubtags = [];

			if (tag.length > 0) {
				this.prototype.tag.forEach(tag => {
					if (Object.prototype.hasOwnProperty.call(this.tags, tag)) this.availableSubtags.push(...this.tags[tag])
				});
			}

			if (this.availableSubtags.length === 0) this.prototype.subtag = null;
		},
		valuesWaiter() {
			let waiter = new Promise((resolve, reject) => {
				this.$on("invalid", reject);
				this.$on("value", data => this.prototype[data.name] = data.value || null);
				setInterval(resolve, 200);
			});

			this.$emit("submit");
			return waiter;
		},
		submitForm() {
			this.valuesWaiter().then(() => {
				if (!this.$route.params.id || this.$route.params.id === "new") {
					this.$database.append('prototypes/', this.prototype)
						.then(() => {
							this.$eventbus.$emit("alert", { type: "success", message: `Prototype ${ this.prototype.title } created!` });
						}) 
						.catch((err) => {
							console.error(err);
							this.$eventbus.$emit("alert", { type: "danger", message: "Error creating prototype. Check console for more details" });
						});
				} else {
					this.$database.update('prototypes/', this.$route.params.id, this.prototype)
						.then(() => {
							this.$eventbus.$emit("alert", { type: "success", message: `prototype ${ this.prototype.title } updated!` });
						})
						.catch((err) => {
							console.error(err);
							this.$eventbus.$emit("alert", { type: "danger", message: "Error updating prototype. Check console for more details" });
						});
				}
				
				this.$el.classList.add("submited");
			 	this.$router.push({ name: "prototypes" });
			}).catch(console.err);
		}
	},
	components: {
		InputForm,
		SelectForm,
		MultipleSelectForm,
		TextareaForm,
		LinkListInput,
		StepsInput
	}
}
</script>


